.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/*Норм меню*/

ul {
  padding: 0;
}

.top-header {
  background-color: transparent;
}

.top-header .wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  justify-content: center;
  align-items: center;
  height: 50px;
}

.top-header .wrapper .item {
  width: calc(33.33333% - 30px);
  display: flex;
  justify-content: center;
}

.top-header .wrapper .item a,
.top-header .wrapper .item .fa {
  color: #F0F5FC;
}

.nav {
  background-color: transparent;
}

.nav-wrapper {
  background-color: transparent;
  display: flex;
  flex-direction: row;
}

.nav-brand {
  width: 240px;
  line-height: 50px;
  z-index: 4;
  display: block;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0 15px;
  justify-content: center;
  align-items: center;
  color: #F0F5FC;
}

.nav-toggler {
  display: none;
  /*Не работает*/
}

.nav-toggler:checked ~ .nav_content {
  transform: translateY(0);
  opacity: 1;
  max-height: 2000px;
  transition: all 1s ease-in;
}

.nav-toggler:checked ~ .nav-toggler-label:after {
  content: '\2715';
}

.nav-toggler-label {
  z-index: 4;
  display: none;
  cursor: pointer;
  padding: 5px 10px;
  color: #D9D9D9;
  box-shadow: 0 0 3px #D9D9D9;
}

.nav-toggler-label:after {
  content: '\2630';
  text-decoration: none;
  font-size: 22px;
}

.nav_content {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.nav_content:hover {
  display: flex;
}

.nav-active {
  transition: .5s;
  box-shadow: 0 0 3px #4d4d4d;
}

.nav-active a {
  color: #F0F5FC !important;
}

.nav-ul_wrapper {
  list-style: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 100%;
}

.nav-ul_wrapper li {
  transition-duration: .5s;
}

.nav-ul_wrapper li a {
  display: block;
  padding: .8rem;
  color: #F0F5FC;
  transition: .5s;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
}

.nav-ul_wrapper li a.btn {
  color: rgba(0, 0, 0, 0.9);
  background-color: #fff;
  border-left: 1px solid #333333;
  border-right: 1px solid #333333;
}

.nav-ul_wrapper li a.btn:hover {
  background-color: #4d4d4d;
}

.nav-ul_wrapper li:hover {
  transition-duration: .5s;
  box-shadow: 0 0 3px #4d4d4d;
}

.nav-ul_wrapper li:hover a {
  color: #F0F5FC;
}

.nav .search-wrap .search {
  padding: 0 15px;
  text-align: right;
}

.nav .search-wrap .search i {
  color: #F0F5FC;
  cursor: pointer;
  transition-duration: .5s;
}

.nav .search-wrap .search i:hover {
  color: #4D4F7A;
  transition-duration: .5s;
}

.search-field {
  background-color: #333333;
  display: none;
  padding: 15px;
  box-shadow: 0 0 3px #4d4d4d;
}

.search-field .nav-form {
  position: relative;
}

.search-field input {
  width: calc(83.33333% - 30px);
  height: 35px;
  padding-left: 10px;
  border: none;
  border-radius: 3px;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.9);
  margin-top: 0;
}

.search-field button {
  width: calc(16.66667% - 30px);
  height: 35px;
  border: none;
  background-color: rgba(255, 255, 255, 0.9);
  box-sizing: border-box;
  text-transform: uppercase;
  line-height: 40px;
  font-size: 16px;
  cursor: pointer;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
