code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  background-color: #333333;
  background-image: url(/assets/images/bg.jpg);
  background-size: cover;
  height: 100vh;
  width: 100vw;
  color: #333333;
  font-family: 'Roboto',sans-serif;
}

.date {
  font-size: 24px;
  text-transform: uppercase;
}

.month {
  font-size: 20px;
  text-transform: uppercase;
}

.time {
  font-size: 24px;
  text-transform: uppercase;
}

ul.event > li {
  background-image: url(/assets/images/geks.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  position: absolute;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  background: #F0F5FC;
  z-index: 10;
  text-align: center;
  border-radius: 15px;
  clip-path: polygon(25% 5%, 75% 5%, 100% 50%, 75% 95%, 25% 95%, 0% 50%);
}

ul.event > li .date,
ul.event > li .month {
  font-weight: 500;
}

ul.event > li .place,
ul.event > li .time,
ul.event > li .btn {
  display: none;
  opacity: 0;
}

ul.event > li.active {
  left: calc(50% - 170px);
  right: 50%;
  top: calc(50% - 170px);
  height: 340px;
  width: 340px;
  padding: 100px 10px;
  transition: all 300ms ease-in-out;
}

ul.event > li.active .place,
ul.event > li.active .time,
ul.event > li.active .btn {
  display: block;
  opacity: 1;
}

ul.event > li.active .btn {
  display: inline;
}

ul.event > li.active .place {
  font-size: 22px;
}

ul.event > li.active .month {
  display: inline-block;
  font-size: 34px;
  font-weight: 500;
}

ul.event > li.active .date {
  font-weight: 500;
  font-size: 34px;
  display: inline-block;
}

.none {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0px;
  top: 0;
}

.small {
  position: absolute;
  height: 150px;
  width: 150px;
  padding: 50px 10px;
  transition: all 300ms ease-in-out;
}

.small_top {
  top: 85px;
  right: 380px;
}

.small_bottom {
  left: 380px;
  bottom: 85px;
}

.middle {
  position: absolute;
  height: 200px;
  width: 200px;
  padding: 70px 10px;
  transition: all 300ms ease-in-out;
}

.middle_top {
  top: 215px;
  right: 560px;
}

.middle_bottom {
  left: 560px;
  bottom: 215px;
}

.btn {
  text-decoration: none;
}

.btn-info {
  color: #333333;
  border: 1px solid #333333;
  border-radius: 5px;
  padding: 5px 10px;
  margin-top: 15px;
}

.rival {
  background-color: #fff;
  position: absolute;
  height: 150px;
  width: 40%;
  color: #333333;
  line-height: 130px;
  padding: 10px 30px;
  box-sizing: border-box;
  font-size: 36px;
  font-weight: bold;
  border-radius: 10px;
}

.rival-first {
  text-align: right;
  left: 0;
  top: 330px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.rival-first:before {
  content: '';
  position: absolute;
  width: 100px;
  height: 100%;
  border-radius: inherit;
  transform: skewX(-29deg);
  right: -40px;
  bottom: 0;
  z-index: -1;
  background-color: inherit;
}

.rival-second {
  text-align: left;
  right: 0;
  top: 484px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.rival-second:before {
  content: '';
  position: absolute;
  width: 100px;
  height: 100%;
  border-radius: inherit;
  transform: skewX(-29deg);
  left: -40px;
  bottom: 0;
  z-index: -1;
  background-color: inherit;
}

.time {
  margin-bottom: 15px;
}

.event_rivals {
  opacity: 0;
  transition: 1s all ease;
}

.show {
  opacity: 1;
}

@media screen and (max-width: 960px) {
  .nav .search-wrap {
    width: auto;
  }
}

@media screen and (max-width: 780px) {
  .nav_content {
    flex-direction: column;
  }
}

@media screen and (max-width: 560px) {
  .top-header .wrapper .item {
    width: calc(50% - 30px);
  }

  .top-header .wrapper .item {
    font-size: 12px;
  }

  .top-header .wrapper .item:nth-child(2) {
    display: none;
  }

  .nav {
    padding: 5px;
  }

  .nav-wrapper {
    flex-wrap: wrap;
  }

  .nav-wrapper {
    justify-content: space-between;
  }

  .nav-brand {
    display: flex;
  }

  .nav-toggler-label {
    display: block;
  }

  .nav_content {
    display: none;
  }

  .nav-ul_wrapper {
    flex-direction: column;
  }

  .nav-ul_wrapper li {
    width: 100%;
  }

  .nav .search-wrap {
    display: none;
  }

  .search-field {
    background-color: #fff;
  }

  .search-field {
    box-shadow: none;
  }

  .search-field {
    display: none;
  }

  .search-field input {
    border: 1px solid rgba(0, 0, 0, 0.1);
  }

  .search-field button {
    width: 23%;
  }

  .search-field button {
    border: 1px solid #333333;
  }

  .search-field button {
    line-height: 35px;
  }

  .rival {
    width: 100%;
  }

  .rival-first {
    top: 60px;
  }

  .rival-second {
    top: unset;
    bottom: 60px;
  }
}